import Toast from './toast.vue'
export default {
  install(Vue) {
    console.log(Vue)
    Vue.prototype.$toast = function (message, propsData) {
      // vue
      let Profile = Vue.extend(Toast)
      //  Profile Dom $el
      let toast = new Profile({ propsData })
      // 
      toast.$slots.default = message
      toast.$mount()
      //body
      document.body.appendChild(toast.$el)
    }
  },
}
